import VideoDialog from '@/components/dialog/VideoDialog';
import { getUUId } from '@/utils/util';
import { getWmsStatic } from '@/api/modules/wms';
export default {
  name: 'Banner',
  components: {
    VideoDialog: VideoDialog
  },
  props: {
    field: {
      type: String,
      default: 'index_new'
    }
  },
  data: function data() {
    return {
      bannerList: [],
      swiperOption: {
        loop: true,
        autoplay: {
          delay: 5000,
          disableOnInteraction: false
        },
        on: {
          beforeLoopFix: function beforeLoopFix() {
            console.log('before');
          },
          loopFix: function loopFix() {
            console.log('fix');
          }
        }
      },
      video: null,
      poster: null,
      showVideoDialog: false
    };
  },
  created: function created() {
    this.getBannerList();
  },
  methods: {
    getBannerList: function getBannerList() {
      var _this = this;

      var url = '/_banner/banner.txt';
      var params = {
        params: {
          _: getUUId()
        }
      };
      getWmsStatic(url, params).then(function (_ref) {
        var data = _ref.data;
        var bannerList = data[_this.field].slider_data;
        bannerList.forEach(function (item) {
          item.imgUrl = "".concat(window.dynamicConfig.wmsServer, "/").concat(item.imgurl);
          item.imgUrl += window.isSupportWebp ? '_.webp' : '';
        });
        _this.bannerList = bannerList;
      });
    },
    openVideoDialog: function openVideoDialog(video, poster) {
      this.poster = poster;
      this.video = video;
      this.showVideoDialog = true;
    },
    handelClick: function handelClick(item, index) {
      if (item.action.detail) {
        switch (item.action.type) {
          case '1':
            this.$router.push(item.action.detail);
            break;

          case '2':
            // 进入游戏
            break;

          case '3':
            this.openVideoDialog(item.action.detail, item.imgUrl);
            break;
        }
      }

      console.log(item, index);
    }
  }
};