import http from '@/api/http.js';
import { baseGateway, productGateway } from '@/api/api_config.js'; // 获取余额

export function getBaccaratRank(params, headers) {
  return http.post("".concat(productGateway, "/baccarat/get-new-rank"), params, headers);
}
export default {
  getBaccaratRank: getBaccaratRank
};